import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Email from "../components/SendEmail.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/Email",
    name: "Email",
    component: Email
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
